/* colors used:
  #fdfdff
  #ddd
  #02558b
  #2cf
  #ff533d
*/

.BaseLayout {

}

.topnav {
  background: -webkit-linear-gradient(295deg, #02558b 30%, #2cf );
  background: -o-linear-gradient(295deg, #02558b 30%, #2cf );
  background: linear-gradient(155deg, #02558b 30%, #2cf );
  height: 55px;
  overflow: hidden;
  -webkit-transition: height 0.5s;
  -o-transition: height 0.5s;
  transition: height 0.5s;
  width: 100%;

  /* margin-bottom: 1rem; */
}

.topnav a:not(:first-child) {
  display: none;
  opacity: 0;
  height: 0;
}

.topnav a {
  color: #f2f2f2;
  float: left;
  display: block;
  text-align: center;
  padding: 1.2rem 1rem;
  text-decoration: none;
  font-size: 17px;
  font-family: 'Righteous', sans-serif;
  text-transform: capitalize;
}

.topnav .Logo {
  margin-left: -1.4rem;
  padding-left: 1rem;
  padding-top: 0.75rem;
  font-family: 'Righteous', sans-serif;
  width: 50%;
}

.Logo img {
  margin-right: 0.5rem;
  width: 15%;
}

.topnav .icon {
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  float: right;
  font-size: 24px;
  height: 2rem;
  opacity: 1;
  position: absolute;
  right: 1rem;
  text-align: center;
  top: 0.7rem;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  width: 2rem;
}

.topnav .GamesLink, .topnav .NewGameLink, .topnav .LogOutLink, .topnav .AboutLink {
  -webkit-animation: 0.9s ease-in 0s 1 slideOutToLeft;
          animation: 0.9s ease-in 0s 1 slideOutToLeft;
}

.topnav a:hover {
  background-color: transparent;
}

.topnav.responsive {
  height: 250px;
  -webkit-transition: height 0.5s;
  -o-transition: height 0.5s;
  transition: height 0.5s;

  /* border-bottom: 2px solid white; */
}

.topnav.responsive .icon {
  color: white;
  /* position: absolute; */
  height: 2rem;
  /* right: 0.5rem;
  top: 0.5rem; */

  width: 2rem;
}

.topnav.responsive a {
  float: none;
  opacity: 1;
  height: 20px;
  text-align: left;
}

.topnav.responsive .Logo {
  margin-left: 0rem;
  padding-left: 1rem;
}

/*.topnav.responsive a:hover {
    background-color: #ddd;
}*/

.topnav.responsive .GamesLink {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 0.5s ease-out 0s 1 slideInFromLeft;
          animation: 0.5s ease-out 0s 1 slideInFromLeft;
  margin-top: 8px;
  vertical-align: top;
}

.topnav.responsive .NewGameLink {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 0.7s ease-out 0s 1 slideInFromLeft;
          animation: 0.7s ease-out 0s 1 slideInFromLeft;
  margin-top: 8px;
}

.topnav.responsive .AboutLink {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 0.9s ease-out 0s 1 slideInFromLeft;
          animation: 0.9s ease-out 0s 1 slideInFromLeft;
  margin-top: 8px;
}

.topnav.responsive .LogOutLink {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: 1.1s ease-out 0s 1 slideInFromLeft;
          animation: 1.1s ease-out 0s 1 slideInFromLeft;
  margin-top: 8px;
}

.icon.rotate {
  transform: rotate(90deg);
  /* Safari */
  -webkit-transform: rotate(90deg);
  /* Firefox */
  -moz-transform: rotate(90deg);
  /* IE */
  -ms-transform: rotate(90deg);
  /* Opera */
  -o-transform: rotate(90deg);

  -webkit-transition: -webkit-transform 0.5s;

  transition: -webkit-transform 0.5s;

  -o-transition: transform 0.5s;

  transition: transform 0.5s;

  transition: transform 0.5s, -webkit-transform 0.5s;
}



