/***********************************
Game Filter bar Styling
************************************/

/* colors used:
  #fdfdff
  #ddd
  #02558b
  #2cf
  #ff533d or rgba(255, 83, 61, 1)
  #CC200A
*/

.GameList .filter-bar {
  background-image: url('../../images/gplaypattern.png');
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 0.7rem;
}

.GameList .filter-bar a {
  -ms-flex-preferred-size: 18%;
      flex-basis: 18%;
  text-align: center;
  -webkit-transition: color 0.4s;
  -o-transition: color 0.4s;
  transition: color 0.4s;

  /* color: white; */
}

.filter_header {
  color: darkgray;
  font-size: 16px;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  text-align: center;

  /* color: white; */
}

.filter-bar .material-icons {
  font-size: 48px;

  /* color: #ff533d; */
  /* -webkit-text-stroke: 2px #9938f9; */
}

.filter-bar .card_hand {
  -webkit-transform: scale(-1, -1);
      -ms-transform: scale(-1, -1);
          transform: scale(-1, -1);
}

.filter-bar p {
  font-size: 14px;
  text-align: center;
  -webkit-transition: color 0.4s;
  -o-transition: color 0.4s;
  transition: color 0.4s;

  /* color: white; */
}

.filter-bar .badge-pill {
  background-color: #ff7043;
  color: white;
  font-size: 12px;
  min-width: 50%;
}



